<template>
  <v-container>
    <v-row class="center" justify="center">
      <v-col cols="12">
        <!--  ===============================================TABOVI ===============================================-->
        <v-tabs
          background-color="white"
          class="elevation-2 primary--text"
        >
          <v-tabs-slider color="primary"></v-tabs-slider>
          <!--      =============================================== Evidencije ===============================================-->
          <v-tab>
            <v-icon>mdi-clipboard-check-multiple-outline</v-icon>
            <v-card-text class="text-uppercase">
              {{$t('drawer.evidentions')}}
            </v-card-text>
          </v-tab>
          <v-tab-item>
            <evidentions/>
          </v-tab-item>
          <!--      =============================================== Radnici ===============================================-->
          <v-tab>
            <v-icon>mdi-worker</v-icon>
            <v-card-text class="text-uppercase">
              {{$t('drawer.workers')}}
            </v-card-text>
          </v-tab>
          <v-tab-item>
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col cols="5">
                    <v-combobox
                      multiple
                      v-model="workerType"
                      :items="workerTypes"
                      :label="'Tip radnika'"
                      item-text="name"
                      autocomplete="off"
                    />
                  </v-col>
                  <v-col cols="5">
                    <v-combobox
                      multiple
                      v-model="worker"
                      :items="workers"
                      :label="'Ime radnika'"
                      item-text="fullName"
                      autocomplete="off"
                    />
                  </v-col>
                  <v-col>
                    <v-layout align-center justify-center>
                      <v-btn class="primary" @click="downloadWorkerReport" :loading="downloadingWorkerReport">Preuzmi izvještaj</v-btn>
                    </v-layout>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs>
      </v-col>
    </v-row>

  </v-container>
</template>

<script>
    import axios from 'axios'
    import {
        readWorkers,
        readWorkerTypes
    } from "../../statics/DataTableFunctions";
    import EvidentionReports from "../../components/reports/EvidentionReports";

    export default {
        name: "Excel",
        components: {
            'evidentions': EvidentionReports
        },
        data() {
            return {
                downloadingWorkerReport:false,
                worker: null,
                workers: null,
                workerType: null,
                workerTypes: null,
            }
        },
        created() {
            this.readWorkerTypes()
            this.readWorkers()
        },
        methods: {
            readWorkers,
            readWorkerTypes,
            downloadWorkerReport() {
                if((this.worker === null || this.worker === []) && (this.workerType === null || this.workerType === [])){
                    return
                }
                this.downloadingWorkerReport=true
                const FileDownload = require('js-file-download');
                axios({
                    url: 'api/downloadReportExcel',
                    method: "POST",
                    data: {
                        workers:this.worker === [] ? null : this.worker,
                        worker_types:this.workerType === [] ? null : this.workerType
                    },
                    responseType: "blob" // important
                }).then(response => {
                    this.downloadingWorkerReport=false
                    FileDownload(response.data, 'File.xlsx');
                }).catch(error => {
                    this.downloadingWorkerReport=false
                    this.$swal.fire({
                        type: "error",
                        title: "Error",
                        text: error
                    });
                });
            }
        }
    }
</script>

<style scoped>

</style>
